<template>
<div>
  <Nav />
  <div>
    {{ users }}
  </div>
</div>

</template>

<script>
import Nav from './Nav.vue'
import axios from 'axios'

export default {
  data () {
    return {
      users: this.getUsers()
    }
  },
  methods: {
    getUsers () {
      const path = 'http://localhost:5000/api/users'
      axios.get(path)
        .then(response => {
          this.hosts = response.data
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  created () {
    this.getUsers()
  }
}
</script>

<style>
.title {
  margin-bottom: 1.5rem;
}
</style>



